$mautim: #87509c; //Tím
$maungoc: #17c2a4; // Cyan
$mauvang: #ffdd99; // Vang
$maudo: #d74680; // Đỏ
$mauxanh: #3c5499; // Xanh
$home_slider_btn: #eb7d4b;
$team: #e7f1f8;
$chuxanh: #3c4761;


nav {
    background: $mautim;
    min-height: 120px;
    display: flex;
    align-items: center;
    text-align: center;
    .nav_wrap {
        display: flex;
        ul {
            margin-left: auto;
            list-style: none;
            li {
                text-align: center;
                display: inline-block;
                margin: 10px 10px;
                width: 70px;
                border-radius: 5px;
                padding: 3px 0 6px 0;
                a {
                    color: white;
                    font-size: .8rem; } }
            .active {
                background: #643a79; } } } }


.home_slider {
    background: $mautim;
    min-height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h1 {
        font-size: 2.3rem;
        color: white;
        letter-spacing: 1px; }
    .btn_more {
        background: $home_slider_btn;
        font-size: 1rem;
        color: white;
        box-shadow: 0px 5px #df6a34; } }

.home_services {
    background: $maungoc;
    min-height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
    padding: 2rem;
    .services_title {
        h2 {
            position: relative;
            margin-bottom: 1rem;
            &:before,
            &:after {
                content: '';
                display: block;
                background: grey;
                position: absolute;
                left: 50%;
                transform: translateX(-50%); }
            &:before {
                height: 3px;
                width: 60px;
                bottom: -8px; }
            &:after {
                height: 10px;
                width: 10px;
                bottom: -12px; } } }
    img {
        max-width: 150px;
        margin: 1.5rem 0; } }
.home_team {
    background: $team;
    min-height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: $chuxanh;
    padding: 2rem;
    .team_title {
        h2 {
            position: relative;
            margin-bottom: 1rem;
