                width: 10px;
                bottom: -12px; } } }
    img {
        max-width: 200px;
        margin: 20px 0;
        border-radius: 50%; }
    .team_items {
        .title {
            font-size: .9rem;
            color: #8cd1ee; }
        i {
            display: inline-block;
            color: white;
            border-radius: 50%;
            background-color: lightgrey;
            height: 30px;
            line-height: 30px;
            margin: auto 5px;
            width: 30px;
            font-size: 15px;
            text-align: center; } } }

